@import (reference) "constants.less";

body {
    border:0;
    padding:0;
    margin:0;
    
    font-size: @font-size;
    font-family: @font-family;
    line-height: @line-height;
    color: @color-dark;
}

.scroll {
    overflow: auto;
}

.noscroll {
    overflow: hidden;
}

.block {
    display: block !important;
}

.list-flat {
    list-style: none;
    padding:0;
    margin:0;
}

.absolute {
    position: absolute;
}

.bold {
    font-weight: bold;
}

.cover {
    top: 0;
    bottom:0;
    left:0;
    right:0;
}

.relative {
    position: relative;
}

hr {
    height: 1px;
    background-color: @color-light;
    border:none;
    width: 100%;
}


@import "./form";
@import "./buttons";
@import "./color";
@import "./flex";
@import './type';
@import './space';
@import './shadow';
@import './table';


